body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

.staticStyle {
  height: 30px;
  border-radius: 5px;
  padding-top: 10px;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 10px;
  margin-bottom: 10px;
}

.full {
  color: #fff;
  background-color: #000;
  border: 2px solid #000;
}

.greenTurn {
  color: green;
  background-color: #fff;
  border: green 2px solid;
}

.green {
  color: #fff;
  background-color: green;
  border: green 2px solid;
}

.greenTurn:hover {
  color: #fff;
  background-color: green;
}

.blue {
  border: blue 2px solid;
  color: #fff;
  background-color: blue;
}

.blueTurn {
  border: blue 2px solid;
  color: blue;
  background-color: #fff;
}

.blueTurn:hover {
  color: #fff;
  background-color: blue;
}

.square {
  background: #fff;
  border: 1px solid #999;
  border-radius: 5px;
  float: left;
  font-size: 24px;
  line-height: 34px;
  height: 80px;
  margin: 5px;
  width: 80px;
}

.square:hover {
  cursor: pointer;
  height: 90px;
  margin: 0px;
  width: 90px;
}

.square:focus {
  outline: none;
}

.restart {
  width: 80%;
  margin: 300px 10%;
  padding: 10px;
  display: block;
  color: #000;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 20px;
  cursor: pointer;
}

.restart:hover {
  color: #fff;
  background-color: #000;
}

.game {
  display: flex;
  justify-content: center;
  flex-direction: row;
}

